@import "https://fonts.googleapis.com/css2?family=Alkatra&family=Noto+Sans+KR&display=swap";
body {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(90deg, #f5ed00 70%, #e6de00 70%), #fff700;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Cyber, sans-serif;
  display: flex;
}

#header {
  background: url("cb.21e93f1a.gif");
  border: 5px solid #000;
  border-radius: 5px;
  display: flex;
}

#logo {
  width: 70px;
}

#title {
  color: #19e0fa;
  text-align: center;
  margin: auto;
  padding: 5px;
}

#postMessageContainer {
  display: none;
}

#profileInfoContainer .test {
  display: none !important;
}

#deleteAccBtn, #displayUsernames, #statusUpdatesContainer {
  max-width: 300px;
  display: none;
}

#statusUpdatesContainer p {
  max-width: 300px;
  background-color: #03d4d477;
  border: 5px solid #000;
  border-radius: 12px;
  padding: 10px;
  display: block;
}

#deleteAccBtn {
  margin: auto;
}

#inputUsername, #inputPassword, #avatarIMGS, #labelForSelect, #inputForMessages {
  text-align: center;
  margin: auto;
  font-family: Cyber, sans-serif;
  font-size: 20px;
  display: block;
}

#avatarIMGS {
  color: #ff0;
  background-color: #000;
  margin-top: -5px;
  margin-bottom: 10px;
}

#inputUsername, #inputPassword, #inputForMessages {
  color: #19e0fa;
  background-color: #000;
  margin-top: 15px;
}

#inputUsername::placeholder, #inputPassword::placeholder, #inputForMessages::placeholder {
  color: #ff0;
}

#inputPassword {
  margin-top: -15px;
}

#inputForMessages {
  margin-bottom: 15px;
}

#buttons {
  margin-top: -30px;
}

#loggedInAs {
  margin-top: -45px;
  font-size: 30px;
  text-decoration: underline;
}

#containerForLoggedIn {
  margin-top: 15px;
}

#displayUsernames {
  float: right;
  margin-right: 15rem;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

#users {
  color: #19e0fa;
  cursor: pointer;
  background-color: #000;
  border-radius: 12px;
  margin-top: 10px;
  padding: 10px;
}

#users:hover {
  color: #fff;
  scale: 1.05;
}

#profileIMG {
  border: 3px solid #19e0fa;
  border-radius: 12px;
}

#profileInfoContainer {
  width: 300px;
}

#containerForLoggedIn {
  float: left;
  margin-left: 15px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

#headerBanner {
  width: 100%;
  height: 50px;
}

#loginBtn, #createUserBtn {
  margin: 10px;
}

@media only screen and (width <= 900px) {
  #contentContainer, #userNameContainer {
